import { create } from "zustand"
import { persist } from "zustand/middleware"
import { NotaIngresoService, mapBackendToFrontend } from "@/lib/api/nota_ingreso.service"

export type IngresoStatus = "paletizado" | "validado" | "almacenado" | "anulado"
export type IngresoTipo = "produccion" | "traspaso" | "reingreso" | "anulacion"

export interface IngresoItem {
  id: string
  producto: string
  descripcion?: string
  cantidad: number
  lote: string
  vencimiento: string
}

export interface Ingreso {
  id: string
  documento: string
  tipo: IngresoTipo
  origen: string
  fecha: string
  items: IngresoItem[]
  estado: IngresoStatus
  observaciones: string
  usuarioCreacion: string
  usuarioValidacion?: string
  usuarioAlmacenaje?: string
  almacenId: string
}

